<template>
    <div class="edit container">
        <alert :message="alert" v-if="alert"></alert>
        <h1 class="page-header">
            编辑用户
        </h1>
        <form v-on:submit="updateCustomers">
            <div class="well">
                <h4>用户信息</h4>
                <div class="form-group">
                    <label >姓名</label>
                    <input type="text" class="form-control" placeholder="name" v-model="customer.name">
                </div>
                <div class="form-group">
                    <label >电话</label>
                    <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
                </div>
                <div class="form-group">
                    <label >邮箱</label>
                    <input type="text" class="form-control" placeholder="email" v-model="customer.email">
                </div>
                <div class="form-group">
                    <label >学历</label>
                    <input type="text" class="form-control" placeholder="education" v-model="customer.education">
                </div>
                <div class="form-group">
                    <label >毕业学校</label>
                    <input type="text" class="form-control" placeholder="college" v-model="customer.college">
                </div>
                <div class="form-group">
                    <label >职业</label>
                    <input type="text" class="form-control" placeholder="occupation" v-model="customer.occupation">
                </div>
                <div class="form-group">
                    <label >个人简介</label>
                    <textarea rows="10" class="form-control" placeholder="resume" v-model="customer.resume"></textarea>
                </div>
                <button type="submit" class="btn btn-primary">修改</button>
            </div>
        </form>
    </div>
</template>

<script>
    import Alert from './Alert'
    export default {
        name: 'edit',
        data() {
            return {
                customer: {},
                id: this.$route.params.id,
                alert:''

            }
        },
        methods: {
            updateCustomers(e) {
                if (!this.customer.name || !this.customer.phone || !this.customer.email) {
                    this.alert ='请输入修改的内容';
                } else {
                    let updateCustomer = {
                        name: this.customer.name,
                        phone: this.customer.phone,
                        email: this.customer.email,
                        education: this.customer.education,
                        college: this.customer.college,
                        occupation: this.customer.occupation,
                        resume: this.customer.resume,
                    };
                    // this.$http.put("http://localhost:3000/users/" + this.id, updateCustomer)
                    this.$http.put("/userList/" + this.id+'.json', updateCustomer)
                        .then((response) => {
                            this.$router.push({path: '/', query: {alert: '用户信息修改成功'}});
                        })

                }
                e.preventDefault();
            }

        },
        created() {
            this.$http.get("/userList/"+this.id+".json")
                .then((res) => {
                        this.customer = res.data;

                        })

                },

        components:{
            Alert
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>


