<template>
    <div class="customers container">
        <alert v-bind:message="alert" v-if="alert"></alert>
        <h1 class="page-header">用户管理系统</h1>
        <input type="text" v-model="search" placeholder="搜索" class="form-control">
        <br>
        <table class="table table-striped">
            <thead>
            <tr>
                <th>姓名</th>
                <th>电话</th>
                <th>邮箱</th>
                <th></th>
            </tr>
            </thead>

            <tbody>
            <tr v-for="customer in filterBy">
                <td>{{customer.name}}</td>
                <td>{{customer.phone}}</td>
                <td>{{customer.email}}</td>
                <td>
                    <router-link :to="'/customer/'+customer.id" class="btn btn-default">详情</router-link>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import Alert from './Alert'
    export default {
        name: 'customers',
        data() {
            return {
                customers:[],
                alert:"",
                search: ''
            }
        },
        methods:{
            fetchCustomers(){
              // this.$http.get("http://localhost:3000/users")
              this.$http.get("/userList.json")
                  .then((res)=>{
                      // this.customers = response.body;vue-resource和axios不同之处
                      return res.data;
                  })
                  .then((data)=>{
                      let customersArray = [];
                      for(let key in data){
                          data[key].id = key;
                          customersArray.push(data[key]);
                      }
                      this.customers = customersArray;

                  })
            }

        },
        computed:{
            filterBy(){
                return this.customers.filter((customer) => {
                    return customer.name.match(this.search) || customer.phone.match(this.search)||customer.email.match(this.search);

                })
            }
        },
        created(){

            if(this.$route.query.alert){
                this.alert = this.$route.query.alert;
            }
            this.fetchCustomers();
        },
        updated(){
            this.fetchCustomers();
        },
        components:{
            Alert
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



